html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}

header {
  -webkit-flex: none;
          flex: none;
}

main {
  -webkit-flex: 1 1 100%;
          flex: 1 1 100%;
  overflow: hidden;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* Inject styles */
.scrollable-table {
  height: 100%; }

.scrollable-table > .scrollable-table-wrapper,
.scrollable-table > .scrollable-table-content > .scrollable-table-wrapper {
  padding: 0px 20px; }

.scrollable-table > .scrollable-table-content {
  position: relative;
  overflow-y: auto;
  top: var(--cell-height);
  height: 100%; }

.scrollable-table > .scrollable-table-content > table {
  margin-top: calc(var(--cell-height)*(-1));
  border-bottom: 2px solid var(--border-color);
  width: 100%; }

/* dirty hack */
.scrollable-table .scrollable-table-content th {
  position: relative;
  top: -100000px; }

table > thead > tr > th > .header-wrapper {
  text-align: left;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  -webkit-align-items: center;
          align-items: center; }

table > thead > tr > th > .header-wrapper .header-positioner {
  -webkit-flex: auto;
          flex: auto;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: var(--cell-padding); }

table > thead > tr > th > .header-wrapper .header-positioner > div {
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block; }

table > thead > tr > th > .header-wrapper .header-positioner:hover {
  width: auto;
  overflow: visible;
  background-color: #fff;
  z-index: 1;
  position: relative; }

table > thead > tr > th > .header-wrapper .header-positioner:hover::before {
  content: '';
  position: absolute;
  top: 0;
  left: -1px;
  right: -1px;
  bottom: 0;
  display: block;
  border: 0px solid #e7e9eb;
  border-width: 0px 1px;
  background-color: #fff;
  box-shadow: 0 1px 4px #e7e9eb;
  z-index: -1; }

table .sorter,
table .sortable-column {
  cursor: pointer; }

table > thead > tr > th.sortable-column,
table > thead > tr > th[class*="sorted"] {
  position: relative;
  padding-right: 30px; }

table > thead > tr > th .sortable-column:hover,
table > thead > tr > th[class*="sorted"].sortable-column {
  text-decoration: underline; }

table > thead > tr > th[class*="sorted"] .sort-image {
  content: '';
  position: absolute;
  display: block;
  right: 12px;
  top: calc(50% - 2px);
  border-style: solid;
  border-width: 1px 0px 0px 1px;
  border-color: #000;
  width: 4px;
  height: 4px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transition: -webkit-transform .3s ease-in-out;
  transition: -webkit-transform .3s ease-in-out;
  transition: transform .3s ease-in-out;
  transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out; }

table > thead > tr > th.sorted-desc .sort-image {
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg); }

table > thead > tr > th .sorter:hover,
table > thead > tr > th:not(:hover) .sorter.sorted {
  text-decoration: underline; }

.selectable-row {
  cursor: pointer; }

table.default-theme {
  --bg-color: #fff;
  --header-ft-size: 12px;
  --cell-ft-size: 14px;
  --v-align: left;
  --cell-padding: 4px 22px 4px 12px;
  --border-color: #e7e9eb;
  --even-bg-color: #f9f9f9;
  --cell-height: 64px; }

table.default-theme * {
  box-sizing: border-box; }

table > thead > tr > th,
table > tbody > tr:not(.inline-details) > td {
  padding: var(--cell-padding);
  text-align: var(--v-align);
  height: var(--cell-height);
  background-color: var(--bg-color); }

table > thead > tr > th {
  font-size: var(--header-ft-size);
  background-color: var(--bg-color);
  position: relative; }

table > tbody > tr > td,
table > thead > tr > th {
  border-bottom: 1px solid var(--border-color); }

table > tbody > tr:nth-child(even) > td {
  background-color: var(--even-bg-color); }

table > tbody > tr > td {
  font-size: var(--cell-ft-size); }

/* Inject adaptive behavior */
.adaptive-col-name {
  display: none; }

@media all and (max-width: 940px) {
  table.adaptive-table, table {
    table-layout: auto !important;
    width: 100%; }
  table.adaptive-table thead, table thead,
  table.adaptive-table .scrollable-fake-table,
  table .scrollable-fake-table {
    display: none; }
  table.adaptive-table tbody tr, table tbody tr {
    display: block;
    border-color: var(--border-color);
    border-style: solid;
    border-width: 1px 1px 0px 1px; }
  table.adaptive-table tbody td, table tbody td {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: var(--bg-color) !important; }
  table.adaptive-table tbody tr:not(:last-child):not(.selected-row), table tbody tr:not(:last-child):not(.selected-row) {
    margin-bottom: 30px; }
  table.adaptive-table tbody :not(.inline-details) td > *, table tbody :not(.inline-details) td > * {
    -webkit-flex: auto;
            flex: auto;
    text-align: right; }
  table.adaptive-table tbody :not(.inline-details) td > :first-child, table tbody :not(.inline-details) td > :first-child {
    text-align: left; }
  table.adaptive-table tbody td > .adaptive-col-name, table tbody td > .adaptive-col-name {
    display: block; }
  .adaptive-scrollable-table .scrollable-table-content, .scrollable-table .scrollable-table-content {
    top: 20px !important; }
  .adaptive-scrollable-table .scrollable-table-content table, .scrollable-table .scrollable-table-content table {
    margin-top: 0 !important;
    border: 0; }
  .adaptive-scrollable-table .scrollable-table-wrapper, .scrollable-table .scrollable-table-wrapper {
    padding: 0 20px 0 8px; }
  .adaptive-scrollable-table .scrollable-table-content, .scrollable-table .scrollable-table-content {
    top: 8px; }
  .scrollable-table .multi-line {
    display: -webkit-inline-flex;
    display: inline-flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end; }
    .scrollable-table .multi-line div + div::before {
      content: ',';
      margin-right: 5px; } }

/* Add effects (not necessary) */
.selectable-row {
  -webkit-perspective: 40em;
          perspective: 40em;
  -webkit-transform: rotateX(0);
          transform: rotateX(0); }

@media all and (min-width: 941px) {
  .selectable-row:not(.selected-row):hover,
  .selected-row {
    -webkit-animation: rotate .3s ease-out;
            animation: rotate .3s ease-out; }
  @-webkit-keyframes rotate {
    0% {
      -webkit-transform: rotateX(30deg);
              transform: rotateX(30deg); }
    50% {
      -webkit-transform: rotateX(60deg);
              transform: rotateX(60deg); }
    100% {
      -webkit-transform: rotateX(0);
              transform: rotateX(0); } }
  @keyframes rotate {
    0% {
      -webkit-transform: rotateX(30deg);
              transform: rotateX(30deg); }
    50% {
      -webkit-transform: rotateX(60deg);
              transform: rotateX(60deg); }
    100% {
      -webkit-transform: rotateX(0);
              transform: rotateX(0); } } }

.selectable-row:not(.selected-row):hover,
.selected-row {
  -webkit-animation: rotate .3s ease-out;
          animation: rotate .3s ease-out;
  color: #3479f1; }

